/*  function httpProxy#
 *  < String        
 *  < String        GET,POST,etc.
 *  < Object        
 *  < Function      callback, 1 
 *  < {
 *      xml:       Bool   xml2json , fasle
 *      gzinflate: Bool   gzinflate , fasle
 *      context:   Any    callbackthis 
 *    }
 *  }
 *  ajaxjsonp
 */

var createElement = require('./createElement')
var ajax          = require('./ajax')
var queryString   = require('./queryString')

var proxyUrl = 'http://zythum.sinaapp.com/mama2/proxy.php'

function httpProxy (url, type, params, callback, opts) {
  opts = opts || {}
  ajax({
    url: proxyUrl,
    param : {
      params: encodeURIComponent(queryString(params)),//
      referrer: encodeURIComponent((opts.referrer || location.href).split('#')[0]),
      url: encodeURIComponent(url.split('#')[0]),
      post: type === 'post' ? 1 : 0,
      xml: opts.xml ? 1 : 0,
      text: opts.text ? 1 : 0,
      gzinflate: opts.gzinflate ? 1 : 0,
      ua: encodeURIComponent(opts.ua || navigator.userAgent)
    },
    jsonp: true,
    callback: callback,
    context: opts.context
  })
}

module.exports = httpProxy