/*  function queryString#
 *  < Object    {a:1,b:2,c:3}
 *  > String    a=1&b=2&c=3
 *  urlquery
 */
function queryString (obj) {
  var query = []
  for (var one in obj) {
    if (obj.hasOwnProperty(one)) {
      query.push([one, obj[one]].join('='))
    }
  }
  return query.join('&')
}
module.exports = queryString